<script>
/* eslint-disable no-prototype-builtins */

import YesNo from '@/components/YesNo';
import Loader from '@/components/Loader';
import File from '@/components/Base64File';
import Btn from '@/components/Button';
import HtmlWithLinks from '@/components/HtmlWithLinks';

export default {
  components: {
    YesNo,
    Loader,
    File,
    HtmlWithLinks,
    Btn,
  },
  data() {
    return {
      inited: false,
      pending: false,
      config: {
        free_appleid: '',
        free_appleid_password: '',
        ignore_compatibility: 'no',
        ask_for_installation_options: 'no',
        enable_psychic_paper: 'no',
        enable_inapp_patch: 'no',
        enable_trainer: 'no',
        allow_malware_installations: 'no',
        p12: null,
        p12_password: null,
        provision: null,
        wildcard_provision: null,
        dev_p12: null,
        dev_p12_password: null,
        dev_provision: null,
        dev_wildcard_provision: null,
        enterprise_cert_id: '0',
        model: '',
        signing_identity_type: 'auto',
        ios_version: '',
        is_opted_out_from_emails: 'no',
      },
      details: {
        is_apple_silicon: '',
        plus_account: {
          status: '',
        },
        ios_version: '',
        free_last_error: null,
        free_appleid: '',
        free_appleid_password: '',
        p12: '',
        provision: '',
        wildcard_provision: '',
        provision_validation_result: false,
        provision_validation_error: '',
        wildcard_provision_validation_result: false,
        wildcard_provision_validation_error: '',
        p12_validation_result: false,
        p12_validation_error: '',
        p12_validation_issues: [],
        provision_validation_issues: [],
        wildcard_provision_validation_issues: [],
        dev_p12: '',
        dev_provision: '',
        dev_wildcard_provision: '',
        dev_provision_validation_result: false,
        dev_provision_validation_error: '',
        dev_wildcard_provision_validation_result: false,
        dev_wildcard_provision_validation_error: '',
        dev_p12_validation_result: false,
        dev_p12_validation_error: '',
        dev_p12_validation_issues: [],
        dev_provision_validation_issues: [],
        dev_wildcard_provision_validation_issues: [],
      },
      devCredentialsProviders: [],
      enterpriseCertificates: [],
    };
  },
  computed: {
    title() {
      return this.$t('CONFIGURE_FEATURES');
    },
    majorIOSVersion() {
      if (!this.config.ios_version) {
        return 0;
      }

      return +this.config.ios_version.split('.')[0];
    },
    basic() {
      return {
        ignore_compatibility: this.$t('FORCE_APP_INSTALLATION'),
        ask_for_installation_options: this.$t('ASK_FOR_CUSTOM_INSTALLATION'),
        is_opted_out_from_emails: this.$t('OPT_OUT_DESCRIPTION'),
      };
    },
    advanced() {
      return {
        allow_malware_installations: this.$t('ALLOW_MALWARE_INSTALLATIONS'),
      };
    },
  },
  methods: {
    save() {
      if (this.pending) {
        return;
      }

      this.pending = true;
      this.$api.saveConfiguration(this.config)
        .then(() => {
          this.$notify({
            type: 'success',
            text: this.$t('FEATURE_CONFIGURATION_SAVED'),
          });
        })
        .then(this.getConfig)
        .then(() => new Promise((resolve) => {
          setTimeout(() => {
            this.$store.dispatch('getDevices')
              .finally(resolve);
          }, 300);
        }))
        .finally(() => {
          this.pending = false;
        });
    },
    reset(param) {
      if (this.pending) {
        return;
      }

      this.pending = true;
      this.$api.saveConfiguration({ [param]: 'yes' })
        .then(this.getConfig)
        .finally(() => {
          this.pending = false;
        });
    },
    getConfig() {
      return this.$store.dispatch('getConfig')
        .then((config) => {
          Object.keys(config)
            .forEach((feature) => {
              if (this.config.hasOwnProperty(feature) && this.config[feature] !== null) {
                this.config[feature] = config[feature];
              }
              if (this.details.hasOwnProperty(feature)) {
                this.details[feature] = config[feature];
              }
            });
          this.inited = true;
        });
    },
    importDevCredetials(href) {
      this.$confirm(this.$t('IMPORT_DEV_CREDENTIALS_PROVIDER_CONFIRMATION'))
        .then(() => {
          window.location.href = href;
        });
    },
  },
  created() {
    this.getConfig();

    this.$api.getDevCredentialsProviders()
      .then((response) => {
        this.devCredentialsProviders = response;
      });
    this.$api.getEnterpriseCertificates()
      .then((response) => {
        this.enterpriseCertificates = [];
        Object.values(response)
          .forEach((cert) => {
            this.enterpriseCertificates[cert.id] = cert.name;
          });
      });
  },
};
</script>

